import { Badge, Calendar } from "antd";
import type { BadgeProps } from "antd";
import frFR from "antd/es/calendar/locale/fr_FR";

import type { Dayjs } from "dayjs";
import dayjs from "dayjs";
import { useEffect, useState } from "react";
import { api } from "../services/api";

interface EventModel {
    key: number;
    date: string;
    name: string;
    type: string;
}

export function Events() {
    const [events, setEvents] = useState<Array<EventModel>>([]);

    useEffect(() => {
        api.events()
            .then((result) => setEvents(result))
            .catch((error) => console.error(error));
    }, []);

    const dateCellRender = (value: Dayjs) => {
        return (
            <ul className="events">
                {calendarItemsRender(events.filter(({ date }) => dayjs(date, "DD/MM/YYYY", "fr").isSame(value, "day")))}
            </ul>
        );
    };
    return <Calendar dateCellRender={dateCellRender} locale={frFR} />;
}

const calendarItemsRender = (events: Array<EventModel>) =>
    events.map(({ key, type, name }: EventModel) => (
        <li key={type + key}>
            <Badge color={typeToColor[type]} text={name} />
        </li>
    ));

const typeToColor: any = {
    training: "blue",
    competition: "red",
};
