import React, { useEffect } from "react";
import { createCustomEqual } from "fast-equals";
import { isLatLngLiteral } from "@googlemaps/typescript-guards";
import { LocatedEvent, LocationModel } from "../../model";

interface MapProps extends google.maps.MapOptions {
    style: { [key: string]: string };
    center: google.maps.LatLngLiteral;
    onClick?: (e: google.maps.MapMouseEvent) => void;
    onIdle?: (map: google.maps.Map) => void;
    children?: React.ReactNode;
    zoom: any;
}

export const TRAVEL_OPTIONS = [
    { label: "Piéton", value: "WALKING" }, //google.maps.TravelMode.WALKING },
    { label: "Vélo", value: "BICYCLING" }, //google.maps.TravelMode.BICYCLING },
    { label: "Transports", value: "TRANSIT" }, //google.maps.TravelMode.TRANSIT },
    { label: "Voiture", value: "DRIVING" }, // google.maps.TravelMode.DRIVING },
];

export const Map: React.FC<MapProps> = ({ onClick, onIdle, children, style, ...options }) => {
    const ref = React.useRef<HTMLDivElement>(null);
    const [map, setMap] = React.useState<google.maps.Map>();

    React.useEffect(() => {
        if (ref.current && !map) {
            setMap(new google.maps.Map(ref.current, {}));
        }
    }, [ref, map]);

    // because React does not do deep comparisons, a custom hook is used
    // see discussion in https://github.com/googlemaps/js-samples/issues/946

    useDeepCompareEffectForMaps(() => {
        if (map) {
            map.setOptions(options);
        }
    }, [map, options]);

    React.useEffect(() => {
        if (map) {
            ["click", "idle"].forEach((eventName) => google.maps.event.clearListeners(map, eventName));

            if (onClick) {
                map.addListener("click", onClick);
            }

            if (onIdle) {
                map.addListener("idle", () => onIdle(map));
            }
        } else {
            console.log("NO MAP");
        }
    }, [map, onClick, onIdle]);

    return (
        <>
            <div ref={ref} style={style} />
            {React.Children.map(children, (child) => {
                if (React.isValidElement(child)) {
                    // set the map prop on the child component
                    // @ts-ignore
                    return React.cloneElement(child, { map });
                }
            })}
        </>
    );
};

export const Marker: React.FC<google.maps.MarkerOptions> = (options) => {
    const [marker, setMarker] = React.useState<google.maps.Marker>();

    React.useEffect(() => {
        if (!marker) {
            setMarker(new google.maps.Marker());
        }

        // remove marker from map on unmount
        return () => {
            if (marker) {
                marker.setMap(null);
            }
        };
    }, [marker]);

    React.useEffect(() => {
        if (marker) {
            marker.setOptions(options);
        }
    }, [marker, options]);

    return null;
};

export const DirectionsRenderer: React.FC<google.maps.DirectionsRendererOptions> = (options) => {
    const [directionRenderer, setDirectionRenderer] = React.useState<google.maps.DirectionsRenderer>();

    React.useEffect(() => {
        if (!directionRenderer) {
            setDirectionRenderer(new google.maps.DirectionsRenderer());
        }

        // remove marker from map on unmount
        return () => {
            if (directionRenderer) {
                directionRenderer.setMap(null);
            }
        };
    }, [directionRenderer]);

    React.useEffect(() => {
        if (directionRenderer) {
            directionRenderer.setOptions(options);
        }
    }, [directionRenderer, options]);

    return null;
};

// @ts-ignore
const deepCompareEqualsForMaps = createCustomEqual((deepEqual: any) => (a: any, b: any) => {
    console.log("EQ", a, b);

    if (
        isLatLngLiteral(a) ||
        a instanceof google.maps.LatLng ||
        isLatLngLiteral(b) ||
        b instanceof google.maps.LatLng
    ) {
        return new google.maps.LatLng(a).equals(new google.maps.LatLng(b));
    }

    // TODO extend to other types

    // use fast-equals for other objects
    return deepEqual(a, b);
});

function useDeepCompareMemoize(value: any) {
    const ref = React.useRef();

    if (!deepCompareEqualsForMaps(value, ref.current)) {
        ref.current = value;
    }

    return ref.current;
}

function useDeepCompareEffectForMaps(callback: React.EffectCallback, dependencies: any[]) {
    React.useEffect(callback, dependencies.map(useDeepCompareMemoize));
}
