import React, { useState } from "react";
import { Link, Outlet } from "react-router-dom";
import type { MenuProps } from "antd";
import { Breadcrumb, Layout, Menu, theme } from "antd";
import { TrophyOutlined, EnvironmentOutlined } from "@ant-design/icons";

import "./App.scss";
import { UserContext } from "./contexts";

const { Content, Footer, Sider } = Layout;

function App() {
    const [collapsed, setCollapsed] = useState(false);
    const {
        token: { colorBgContainer },
    } = theme.useToken();

    const items: MenuProps["items"] = [
        {
            key: "trainings",
            label: <Link to={"/entrainement"}>Entrainements</Link>,
            icon: <EnvironmentOutlined />,
        },
        {
            key: "competitions",
            label: <Link to={"/competition"}>Compétitions</Link>,
            icon: <TrophyOutlined />,
        },
    ];

    const user = { id: 1 };

    return (
        <Layout style={{ minHeight: "100vh" }}>
            <Sider collapsible collapsed={collapsed} onCollapse={(value) => setCollapsed(value)}>
                <div style={{ height: 32, margin: 16, background: "rgba(255, 255, 255, 0.2)" }} />
                <Menu theme="dark" defaultSelectedKeys={[]} mode="inline" items={items} />
            </Sider>
            <Layout className="site-layout">
                {/* <Header style={{ padding: 0, background: colorBgContainer }} /> */}
                <Content>
                    <Breadcrumb style={{ margin: "16px 0" }}>
                        <Breadcrumb.Item>User</Breadcrumb.Item>
                        <Breadcrumb.Item>Bill</Breadcrumb.Item>
                    </Breadcrumb>
                    <div className="site-layout-content" style={{ background: colorBgContainer }}>
                        <UserContext.Provider value={user}>
                            <Outlet />
                        </UserContext.Provider>
                    </div>
                </Content>

                <Footer style={{ textAlign: "center" }}>RaidOParis</Footer>
            </Layout>
        </Layout>
    );
}

export default App;
