import React, { useEffect } from "react";
import { LocatedEvent, LocationModel } from "../../model";
import { Marker, Map, DirectionsRenderer } from "./google-maps";

const PARIS_CENTER = {
    lat: 48.861571,
    lng: 2.3613715,
};

const HOUSE = {
    lat: 48.8783642,
    lng: 2.4351717,
};
export function InteractiveMap({
    locations,
    selectedLocation,
    travelMode,
}: {
    locations: Array<LocatedEvent>;
    selectedLocation?: LocationModel | null;
    travelMode: google.maps.TravelMode;
}) {
    const [zoom, setZoom] = React.useState(12); // initial zoom
    const [center, setCenter] = React.useState<google.maps.LatLngLiteral>(PARIS_CENTER);
    const [directions, setDirections] = React.useState<google.maps.DirectionsResult>();
    // const [travelMode, setTravelMode] = React.useState<google.maps.TravelMode>(selectedTravelMode);

    useEffect(() => {
        if (selectedLocation) {
            setCenter(selectedLocation);

            const DirectionsService = new google.maps.DirectionsService();

            DirectionsService.route(
                {
                    origin: new google.maps.LatLng(HOUSE),
                    destination: new google.maps.LatLng(selectedLocation),
                    travelMode: travelMode,
                },
                (result, status) => {
                    if (status === google.maps.DirectionsStatus.OK && result) {
                        // console.log(result);
                        setDirections(result);
                    } else {
                        console.error(`error fetching directions ${result}`);
                    }
                }
            );
        }
    }, [selectedLocation, travelMode]);

    const onIdle = (m: google.maps.Map) => {
        setZoom(m.getZoom()!);
        setCenter(m.getCenter()!.toJSON());
    };

    return (
        <div>
            <Map
                center={center}
                onIdle={onIdle}
                zoom={zoom}
                style={{ flexGrow: "1", height: "100%", minWidth: "10px", minHeight: "500px" }}
                clickableIcons={false}
            >
                {locations.map(({ lat, lng, name }, i) => (
                    <Marker key={i} position={new google.maps.LatLng({ lat, lng })} label={name.charAt(0)} />
                ))}
                {directions && <DirectionsRenderer directions={directions} />}
            </Map>
            {directions && <DirectionsDetails directions={directions} />}
        </div>
    );
}

const DirectionsDetails = ({ directions }: { directions: google.maps.DirectionsResult }) => {
    const legs = directions.routes[0].legs[0];
    return (
        <p>
            <span>{legs.distance?.text}</span>
            <span>{legs.duration?.text}</span>
        </p>
    );
};
