import { Card, Col, Row, Segmented } from "antd";
import Title from "antd/es/typography/Title";
import { useContext, useEffect, useState } from "react";

import { EnvironmentOutlined } from "@ant-design/icons";

import { TrainingModel, UserModel } from "../model";
import { api } from "../services/api";
import { InteractiveMap } from "./map/interactive-map";
import { Link } from "react-router-dom";
import { UserContext } from "../contexts";
import { TRAVEL_OPTIONS } from "./map/google-maps";

export function Trainings() {
    const user: UserModel = useContext(UserContext) as UserModel;

    const [items, setItems] = useState<Array<TrainingModel>>([]);
    const [selectedTraining, setSelectedTraining] = useState<TrainingModel | null>(null);
    const [travelMode, setTravelMode] = useState<google.maps.TravelMode>(google.maps.TravelMode.WALKING); // TODO use local storage

    useEffect(() => {
        api.trainings(user["id"])
            .then((result) => setItems(result))
            .catch((error) => console.error(error));
    }, [user]);

    useEffect(() => {
        if (!selectedTraining) {
            setSelectedTraining(items[0]);
        }
    }, [selectedTraining, items]);

    const onTrainingClick = (item: TrainingModel) => {
        setSelectedTraining(item);
    };

    return (
        <Row justify={"center"} gutter={[8, 8]}>
            <Col span={24}>
                <Title>Entrainements</Title>
            </Col>

            <Col xs={24} md={14} xl={18}>
                <InteractiveMap locations={items} selectedLocation={selectedTraining} travelMode={travelMode} />
            </Col>

            <Col xs={24} md={10} xl={6}>
                <Row justify={"start"} gutter={[8, 8]}>
                    <Col span={24} className="text-center">
                        <Segmented options={TRAVEL_OPTIONS} onChange={(value) => setTravelMode(value as any)} />
                    </Col>

                    {items.map((item) => {
                        const { id, name, date, distance } = item;
                        return (
                            <Col key={id} span={24} onClick={() => onTrainingClick(item)}>
                                <Card
                                    className={selectedTraining?.id === id ? "selected-card" : ""}
                                    size="small"
                                    extra={<Link to={`/entrainement/${id}`}>Details</Link>}
                                    title={
                                        <>
                                            <EnvironmentOutlined />
                                            {` ${name}`}
                                            {/* {` ${name} le ${date}`} */}
                                        </>
                                    }
                                    hoverable
                                    bordered={false}
                                >
                                    {`Le ${date} (${distance.toFixed(1)}km) `}
                                </Card>
                            </Col>
                        );
                    })}
                </Row>
            </Col>
        </Row>
    );
}
