import axios from "axios";

const apiInstance = axios.create({
    baseURL: "http://localhost:5000",
});

const PATHS: any = {
    "/trainings": [
        { id: 1, date: "12/12/2022", name: "VIlette", lat: 48.893707, lng: 2.39059 },
        { id: 2, date: "02/12/2022", name: "Lac st mandé", lat: 48.842099, lng: 2.423551 },
    ],
};

const fakeInstance = {
    get: (path: string) => Promise.resolve({ data: PATHS[path] }),
    post: (path: string) => Promise.resolve({ data: PATHS[path] }),
};

export const api = {
    trainings: (user_id?: number | undefined) =>
        apiInstance.get("/trainings", { params: { user_id } }).then((result: any) => result.data),

    events: () => apiInstance.get("/events").then((result: any) => result.data),
};
